import React from 'react';
import './index.scss';
import LazyLoad from 'react-lazyload';
import { Link } from 'react-router-dom';
import { Row,Col } from 'antd';

export default class Footer extends React.Component{
    render(){
        return(
            <div id="footer">
                <Row>
                    <Col xs={{span:24,offset:0}} sm={{span:22,offset:1}} lg={{span:20,offset:2}} xl={{span:18,offset:3}} xxl={{span:16,offset:4}}>
                        <div className="footer_box1">
                            <Row>
                                <Col xs={24} md={10} lg={10} order={1} >
                                    <section className="footer_img">
                                        <div>
                                            <LazyLoad><img src={require('../../assets/img/unionnet.png')} alt="" className="footer_logo"/></LazyLoad>
                                        </div>
                                        <div>
                                            <LazyLoad><img src={require('./img/weixin.png')} alt="" className="footer_weixin"/></LazyLoad>
                                        </div>
                                    </section>
                                </Col>
                                <Col xs={24} md={14} lg={14} order={2} >
                                    <section className="footer_list">
                                        <Row>
                                            <Col xs={24} md={12} lg={12} order={1}>
                                                <dl>
                                                    <dt></dt>
                                                    <dd><Link to='/unenterprise/introduce'></Link></dd>
                                                    <dd><Link to='/unenterprise/honor'></Link></dd>
                                                    <dd><Link to='/unenterprise/demeanour'></Link></dd>
                                                </dl>
                                            </Col>
                                            <Col xs={24} md={12} lg={12} order={2}>
                                                <dl>
                                                    <dt></dt>
                                                    <dd><Link to='/product/hisinfos'>HIS</Link></dd>
                                                    <dd><Link to='/product/digitization'></Link></dd>
                                                    <dd><Link to='/product/medicalrecord'></Link></dd>
                                                    <dd><Link to='/product/drgdata'>DRG</Link></dd>
                                                </dl>
                                            </Col>
                                        </Row>
                                    </section>
                                </Col>
                            </Row>
                        </div>
                        <div className="footer_box2">
                            <Row>
                                <Col xs={24} md={8} lg={8} order={1}>
                                    <p>Copyright&copy;2020unionnet. All Right Reserved</p>
                                </Col>
                                <Col xs={24} md={8} lg={8} order={1}>
                                    <p>  </p>
                                </Col>
                                <Col xs={24} md={8} lg={8} order={1}>
                                    <p>
                                        <a target="_blank" rel = 'noopener noreferrer' href='http://www.beian.gov.cn/portal/registerSystemInfo?recordcode=31010102003763' > 31010102003763</a>
                                    </p>
                                </Col>
                            </Row>
                        </div>
                    </Col>
                </Row>
            </div>
        )
    }
}