import React from 'react';
import './index.scss';
import { Row, Col, Drawer } from 'antd';
import { NavLink } from 'react-router-dom';
import Menu from '../../components/Menu';
import { GlobalOutlined, BulbOutlined, MenuOutlined } from '@ant-design/icons';

class Header extends React.Component {
    state = {
        visible:false,
        currentState:'Index',// className
    }

    showDrawer = () => {
      this.setState({
        visible: true,
      });
    };
  
    onClose = () => {
      this.setState({
        visible: false,
      });
    };

    componentDidMount() {
        window.addEventListener('resize', this.resize);
    }
    componentWillUnmount(){
        window.removeEventListener('resize',this.resize);
    }
    resize = () =>{
        let width = document.documentElement.clientWidth;
        if(width>992){
            this.setState({visible: false})
        }
    }

    scrollToAnchor = (anchorName) => {
        if (anchorName) {
            // 
            let anchorElement = document.getElementById(anchorName);
            // id
            if (anchorElement) { anchorElement.scrollIntoView({ block: 'start', behavior: 'auto' }); }
        }
    }

    showPop = () => {
        document.body.addEventListener("touchmove", { passive: false });  //passive (preventDefault()) 
        document.body.style.overflow = 'hidden';
    }

    closePop = () => {
        // document.body.removeEventListener('touchmove',self.stopScroll);
        // //passiveios9
        document.body.removeEventListener('touchmove', { passive: true });
        document.body.style.overflow = 'auto';
    }

    handlePush = (url,active) => {
        this.props.history.push(url);
        window.scrollTo(0, 0);
        this.setState({
            currentState:active
        })
    }

    render() {
        const { visible } = this.state;
        return (
            <div id="header">
                <Row>
                    <Col xs={{span:22,offset:1}} sm={{span:22,offset:1}} lg={{span:20,offset:2}} xl={{span:18,offset:3}} xxl={{span:16,offset:4}}>
                        <Row>
                            <Col xs={{ span: 12 }} sm={{ span: 12 }} md={{ span: 6 }} lg={{ span: 4 }}>
                                <div className="header_logo" onClick={() => this.handlePush('/')}>
                                    <img src={require('../../assets/img/unionnet.png')} alt="" />
                                </div>
                            </Col>
                            <Col xs={{ span: 12 }} sm={{ span: 12 }} md={{ span: 4, offset: 14 }} lg={{ span: 15, offset: 5 }} xl={{span:12,offset:8}}>
                                <div className="header_navbox">
                                    <ul>
                                        <li><NavLink activeClassName='active_header' exact to='/'><section className="header_title" onClick={() => this.handlePush()}></section></NavLink></li>
                                        <li>
                                            
                                            <NavLink activeClassName='active_header' to='/unenterprise'><section className="header_title"></section></NavLink>
                                            <div className="navbar_box navbar_box2">
                                                <p><NavLink to='/unenterprise/introduce'></NavLink></p>
                                                <p><NavLink to='/unenterprise/demeanour'></NavLink></p>
                                                <p><NavLink to='/unenterprise/honor'></NavLink></p>
                                            </div>
                                        </li>
                                        <li><NavLink activeClassName='active_header' to='/news'><section className="header_title" onClick={() => { setTimeout(() => { this.scrollToAnchor("news") }, 100) }}></section></NavLink></li>
                                        <li><NavLink activeClassName='active_header' to='/product'><section className="header_title" onClick={() => { setTimeout(() => { this.scrollToAnchor("product") }, 100) }}></section></NavLink>
                                            <div className="navbar_box navbar_box4">
                                                <section className="navbox4_shell">
                                                    <div className="nav_solution">
                                                        <h4><GlobalOutlined /></h4>
                                                        <p onClick={() => { this.props.history.push('/product/hisinfos');}}><i></i>HIS</p>
                                                        <p onClick={() => { this.props.history.push('/product/digitization');}}><i></i></p>
                                                        <p onClick={() => { this.props.history.push('/product/medicalrecord');}}><i></i></p>
                                                        <p onClick={() => { this.props.history.push('/product/drgdata');}}><i></i>DRG</p>
                                                    </div>
                                                    <div className="nav_product">
                                                        <h4><BulbOutlined /></h4>
                                                        <p><span><i></i><NavLink to='/product/hisinfos/hospmsg'>HIS</NavLink></span><span><i></i><NavLink to='/product/digitization/digital'></NavLink></span></ p>
                                                        <p><span><i></i><NavLink to='/product/hisinfos/elerecord'></NavLink></span><span><i></i><NavLink to='/product/digitization/paperless'></NavLink></span></p>
                                                        <p><em className="navbar_box4_line"><i></i></em><span><i></i><NavLink to='/product/digitization/wechatorder'></NavLink></span></p>
                                                        <p><span><i></i><NavLink to='/product/drgdata/performance'>DRG</NavLink></span><span><i></i><NavLink to='/product/digitization/printing'></NavLink></span></p>
                                                        <p><span><i></i><NavLink to="/product/drgdata/payment">DRG</NavLink></span><em className="navbar_box4_line"><i></i></em></p>
                                                        <p><span><i></i><NavLink to='/product/drgdata/fund'>DRG</NavLink></span><span><i></i><NavLink to='/product/medicalrecord/management'></NavLink></span></p>
                                                        <p><span><i></i><NavLink to='/product/drgdata/quality'>DRG</NavLink></span><span><i></i><NavLink to='/product/medicalrecord/hqms'>(HQMS)</NavLink></span></p>
                                                        <p><span><i></i><NavLink to='/product/drgdata/threelevel'></NavLink></span><span><i></i><NavLink to='/product/medicalrecord/code'></NavLink></span></p>
                                                        <p><span><i></i><NavLink to='/product/drgdata/clinical'>DRG</NavLink></span><span><i></i><NavLink to='/product/medicalrecord/cloud'></NavLink></span></p>
                                                    </div>
                                                </section>
                                            </div>
                                        </li>
                                        <li><NavLink activeClassName='active_header' to='/successfulcases'><section className="header_title"></section></NavLink></li>
                                        {/* <li><section className="header_title"></section></li> */}
                                        <li><NavLink activeClassName='active_header' to='/about'><section className="header_title" onClick={() => { setTimeout(() => { this.scrollToAnchor("about") }, 100) }}></section></NavLink></li>
                                    </ul>
                                </div>
                                <div className="header_menu" onClick={() => { this.showDrawer() }}>
                                    <MenuOutlined />
                                </div>
                            </Col>
                        </Row>
                    </Col>
                </Row>
                <Drawer
                    placement='left'
                    getContainer={ '#root' }
                    closable={false}
                    onClose={this.onClose}
                    visible={visible}
                    zIndex={9999}
                >
                    <Menu close={ this.onClose }></Menu>
                </Drawer>
            </div>
        )
    }

}

export default Header;