import React from 'react';
import '../_public_scss/_title.scss';
import './index.scss';
import LazyLoad from 'react-lazyload';
import { Row, Col } from 'antd';
import t0 from './img/honor0.jpg';
import t1 from './img/honor1.jpg';
import t2 from './img/honor2.jpg';
import t3 from './img/honor3.jpg';
import t4 from './img/honor4.jpg';
import t5 from './img/honor5.jpg';


let data = [
    { pic:t0, title:'' },
    { pic:t1, title:'' },
    { pic:t2, title:'CMMI-5' },
    { pic:t3, title:'CCCI' },
    { pic:t4, title:'CCCI' },
    { pic:t5, title:'IAF' },
]
const Honor = () => {
    return (
        <div id='honor'>
            <h4 className='public_enterprise_title'><span> Enterprise honor</span></h4>
            <div className='demeanour_content'>
                <p>20</p>
                <Row gutter={[24, 0]}>
                    {
                        data.map((item, index) => {
                            return (
                                <Col lg={{ span: 8 }} sm={{ span: 12 }} xs={{ span: 24 }} key={index}>
                                    <div className='demeanour_content_img'>
                                        <LazyLoad><img src={item.pic} alt='' /></LazyLoad>
                                        <p>{item.title}</p>
                                    </div>
                                </Col>
                            )
                        })
                    }
                </Row>
                <div className='demeanour_content_tag'>
                    <h4><b></b></h4>
                    <Row>
                        <Col xs={{span:22,offset:1}} md={{span:10,offset:1}} >
                            <ul className='ul_list1'>
                                <li>2019CMMI 5</li>
                                <li>2019HITDRG TOP1</li>
                                <li>2017</li>
                                <li>2014</li>
                                <li>2014</li>
                            </ul>
                        </Col>
                        <Col xs={{span:22,offset:1}} md={{span:10,offset:1}}>
                            <ul className='ul_list2'>
                                <li>2013</li>
                                <li>2011</li>
                                <li>2008</li>
                                <li>2004</li>
                                <li>...</li>
                            </ul>
                        </Col>
                    </Row>
                </div>
            </div>

        </div>
    )
}
export default Honor;