import React from 'react';
import { Collapse } from 'antd';
import { withRouter } from 'react-router-dom';
import './index.scss';


const { Panel } = Collapse;

class CustomMenu extends React.Component{
    handleClick = (e,url) =>{
        e.stopPropagation();
        e.cancelBubble = true;
        this.props.history.push(url);
        this.props.close();
    }

    scrollToAnchor = (anchorName) => {
        if (anchorName) {
            // 
            let anchorElement = document.getElementById(anchorName);
            // id
            if (anchorElement) { anchorElement.scrollIntoView({ block: 'start', behavior: 'auto' }); this.props.close()  }
        }
    }

    state={
        data1:[
            {
                name:'',url:'/unenterprise/introduce'
            },
            {
                name:'',url:'/unenterprise/demeanour'
            },
            {
                name:'',url:'/unenterprise/honor'
            },
        ],
        data2:[
            { name:'HIS',url:'/product/hisinfos'},
            { name:'',url:'/product/digitization'},
            { name:'',url:'/product/medicalrecord'},
            { name:'DRG',url:'/product/drgdata'},
        ],
        data3:[
            { name:'HIS',url:'/product/hisinfos/hospmsg'},
            { name:'',url:'/product/hisinfos/elerecord'},
            { name:'',url:'/product/digitization/digital'},
            { name:'',url:'/product/digitization/paperless'},
            { name:'',url:'/product/digitization/wechatorder'},
            { name:'',url:'/product/digitization/printing'},
            { name:'',url:'/product/medicalrecord/management'},
            { name:'(HQMS)',url:'/product/medicalrecord/hqms'},
            { name:'',url:'/product/medicalrecord/code'},
            { name:'',url:'/product/medicalrecord/cloud'},
            { name:'',url:'/product/drgdata/threelevel'},
            { name:'DRG',url:'/product/drgdata/performance'},
            { name:'DRG',url:'/product/drgdata/payment'},
            { name:'DRG',url:'/product/drgdata/fund'},
            { name:'DRG',url:'/product/drgdata/quality'},
            { name:'DRG',url:'/product/drgdata/clinical'},
        ]
    }
    render(){
        const { data1, data2, data3 } = this.state;
        return(
            <div id='custom_menu' ref='custom_menu'>
                <Collapse  expandIconPosition='right' bordered={false} accordion>
                    <div className="ant-collapse-item ant-collapse-no-arrow">
                        <div className="ant-collapse-header" role="button"  aria-expanded="false" onClick={(e)=>{this.handleClick(e,'/')}}>
                        
                        </div>
                    </div>
                    <Panel header="" key="1" showArrow={true}>
                       {
                           data1.map((item,index)=>(
                            <p key={index} onClick={(e)=>{this.handleClick(e,item.url)}}>{item.name}</p>
                           ))
                       }
                    </Panel>
                    <div className="ant-collapse-item ant-collapse-no-arrow" onClick={() => { this.props.history.push('/'); setTimeout(() => { this.scrollToAnchor("news") }, 100) }}>
                        <div className="ant-collapse-header" role="button"  aria-expanded="false">
                        
                        </div>
                    </div>
                    <Panel showArrow header="" key="2">
                        <Collapse expandIconPosition='right' bordered={false} accordion>
                            <Panel header='' key='4'>
                                {
                                    data2.map((item,index)=>(
                                        <p key={index} onClick={(e)=>{ this.handleClick(e,item.url) }}>{item.name}</p>
                                    ))
                                }
                            </Panel> 
                            <Panel header='' key='5'>
                                {
                                    data3.map((item,index)=>(
                                        <p key={index} onClick={(e)=>{ this.handleClick(e,item.url) }}>{item.name}</p>
                                    ))
                                }
                            </Panel> 
                        </Collapse>
                    </Panel>
                    <div className="ant-collapse-item ant-collapse-no-arrow" onClick={(e)=>{ this.handleClick(e,"/successfulcases") }}>
                        <div className="ant-collapse-header" role="button"  aria-expanded="false">
                        
                        </div>
                    </div>
                    <div className="ant-collapse-item ant-collapse-no-arrow" onClick={() => { this.props.history.push('/'); setTimeout(() => { this.scrollToAnchor("about") }, 100) }}>
                        <div className="ant-collapse-header" role="button"  aria-expanded="false">
                            
                        </div>
                    </div>
                </Collapse>
            </div>
        )
    }
}

export default withRouter(CustomMenu);