import React from 'react';
import { Link } from 'react-router-dom';
import { Row, Col, Typography  } from 'antd';
import LazyLoad from 'react-lazyload';   // 
import './index.scss';
import './hover.css';
import Button from '../../../components/Button';
import img1 from './img/product1.jpg';
import img2 from './img/product2.jpg';
import img3 from './img/product3.jpg';
import img4 from './img/product4.jpg';

const { Paragraph } = Typography;
export default class Product extends React.Component {
    state = {
        product_list: [
            {
                name: 'productbox1',
                key: 'over1',
                type: ['left', 'right'],
                describe: '',
                title: 'HIS',
                content: '',
                picture: img1,
                url: '/product/hisinfos'
            },
            {
                name: 'productbox2',
                key: 'over2',
                type: ['right', 'left'],
                describe: '',
                title: '',
                content: '',
                picture: img2,
                url: '/product/digitization'
            },
            {
                name: 'productbox3',
                key: 'over3',
                type: ['left', 'right'],
                describe: '',
                title: '',
                content: 'HIS',
                picture: img3,
                url: '/product/medicalrecord'
            },
            {
                name: 'productbox4',
                key: 'over4',
                type: ['right', 'left'],
                describe: 'DRG',
                title: 'DRG',
                content: 'DRGDRG',
                picture: img4,
                url: '/product/drgdata'
            },
        ]
    }
    render() {
        const { product_list } = this.state;
        return (
            <div id="product">
                <Row>
                    <Col xs={{span:22,offset:1}} sm={{span:22,offset:1}} lg={{span:20,offset:2}} xl={{span:18,offset:3}} xxl={{span:18,offset:3}}>
                        <h3 className="product_title" key="producttitle"><span></span></h3>
                        {
                            product_list.map((item, index) => {
                                return (
                                    <section className="productbox" id={item.name} key={item.url} >
                                        <Row type="flex" align="middle" justify="center" gutter={[{xs:32,sm:32,lg:48,xl:96}, 0]}>
                                            <Col sm={{ span: 24 }} xs={{ span: 24 }} lg={{ span: 12, order: (index % 2) }}  >
                                                <div className="hovereffect">
                                                    <LazyLoad><img className="img-responsive" src={item.picture} alt="" /></LazyLoad>
                                                    <div className="overlay">
                                                        <h2>{item.title}</h2>
                                                        <p>
                                                            <Link to={item.url}>LINK HERE</Link>
                                                        </p>
                                                    </div>
                                                </div>
                                            </Col>
                                            <Col sm={{ span: 24 }} xs={{ span: 24 }} lg={{ span: 12 }}>
                                                <div className="productbox_words">
                                                    <p className='words_describe'>{item.describe}</p>
                                                    <h3>{item.title}</h3>
                                                    <div className='words_text'>
                                                        <Paragraph ellipsis={{ rows: 4, expandable: true, symbol: '' }}>
                                                            {item.content}
                                                        </Paragraph>
                                                    </div>
                                                    <div className="productbox_btn">
                                                        <Link to={item.url}>
                                                            <Button text={""}></Button>
                                                        </Link>
                                                    </div>
                                                </div>
                                            </Col>
                                        </Row>
                                    </section>
                                )
                            })
                        }
                    </Col>
                </Row>
            </div>
        )
    }
}